import React from "react";
import QuestPresenter from "./QuestPresenter";

export default class extends React.Component {
  constructor(props) {
    super(props);
    const {
      match: {
        params: { id },
      },
    } = props;
    this.state = { id };
  }

  handleSubmit = (event) => {
    event.preventDefault();
    const { id } = this.state;
    const score = parseInt(event.target.elements.Q1.value);
    localStorage.setItem(`score${id}`, score);
    if (id != "8") {
      window.location.hash = `#/question/${parseInt(id) + 1}`;
      window.location.reload();
    } else {
      window.location.hash = "#/result";
    }
  };

  render() {
    const { id } = this.state;
    console.log(id);
    return <QuestPresenter handleSubmit={this.handleSubmit} questionId={id} />;
  }
}
